<h3 id="api" tabindex="-1"><a class="header-anchor" href="#api" aria-hidden="true">#</a> API</h3>
<ul>
<li><code>summaryTrend($from, $to)</code> 概况趋势，限定查询1天数据，即 <code>$from</code> 要与 <code>$to</code> 相同；</li>
<li><code>dailyVisitTrend($from, $to)</code> 访问日趋势，限定查询1天数据，即 <code>$from</code> 要与 <code>$to</code> 相同；</li>
<li><code>weeklyVisitTrend($from, $to)</code> 访问周趋势， <code>$from</code> 为周一日期， <code>$to</code> 为周日日期；</li>
<li><code>monthlyVisitTrend($from, $to)</code> 访问月趋势， <code>$from</code> 为月初日期， <code>$to</code> 为月末日期；</li>
<li><code>visitDistribution($from, $to)</code> 访问分布，限定查询1天数据，即 <code>$from</code> 要与 <code>$to</code> 相同；</li>
<li><code>dailyRetainInfo($from, $to)</code> 访问日留存，限定查询1天数据，即 <code>$from</code> 要与 <code>$to</code> 相同；</li>
<li><code>weeklyRetainInfo($from, $to)</code> 访问周留存， <code>$from</code> 为周一日期， <code>$to</code> 为周日日期；</li>
<li><code>montylyRetainInfo($from, $to)</code> 访问月留存， <code>$from</code> 为月初日期， <code>$to</code> 为月末日期；</li>
<li><code>visitPage($from, $to)</code> 访问页面，限定查询1天数据，即 <code>$from</code> 要与 <code>$to</code> 相同；</li>
</ul>
